import React, { useState, useEffect, useRef, createRef} from 'react';
import pptxgen from "pptxgenjs";
import HimnosLista from '../components/HimnosLista';
import PresentacionLista from '../components/PresentacionLista';
import VistaHimno from '../components/VistaHimno';
import engranajes from '../images/engranajes.svg';
import estrofaBg from '../images/bg.jpg';
import oracionesBg from '../images/oraciones.jpg';
import bienvenida from '../images/bienvenida.jpg';
import anuncios from '../images/anuncios.jpg';
import OtrosElementosLista from '../components/OtrosElementosLista';
import Loader from "react-loader-spinner";

const CrearPage = () => {
    const refColumns = useRef();
    const refContainer = useRef();
    const refBorrarTermino = useRef();
    const otrosButton = useRef();
    const refOtrosElementos = createRef();
    const [loading, setLoading] = useState(false);
    const [busqueda, setBusqueda] = useState("");
    const [himnos, setHimnos] = useState([{ Id: 0, Titulo: "", Cantidad_estrofas: 0, selected: false }]);
    const [titulo, setTitulo] = useState("");
    const [id, setID] = useState(1);
    const [buscarContenido, setBuscarContenido] = useState(false);
    const [otrosElementos, setOtrosElementos] = useState([
        {tipo: "imagen", Titulo: "Bienvenida", Id: "i1", url: bienvenida},
        {tipo: "oracion", Titulo:"Oración Inicial", Id: "o1"},
        {tipo: "oracion", Titulo:"Cena", Id: "o2"},
        {tipo: "oracion", Titulo:"Ofrenda", Id: "o3"},
        {tipo: "oracion", Titulo:"Predicación", Id: "p1"},
        {tipo: "oracion", Titulo:"Meditación", Id: "p2"},
        {tipo: "oracion", Titulo:"Oración Final", Id: "o4"},
        {tipo: "imagen", Titulo: "Anuncios", Id: "i2", url: anuncios}]);
    const [presentacion, setPresentacion] = useState([]);

    const compararListas = (lista) => {
        console.log("comparando listas...");
        presentacion.forEach((fila) => {
            let himnoIndex = lista.findIndex(himno => himno.Id === fila.Id);
            if(himnoIndex !== -1)lista[himnoIndex].selected = true; 
        });
        return lista;
    }

    const buscarHimno = (event) => {
        event.preventDefault();
        setLoading(true);
        //Buscar solo en los títulos de los himnos.
        if(!buscarContenido){
            if(busqueda.length > 0){
                const fetchData = async () => {
                    const result = await fetch('api/buscartitulos?term='+ busqueda, {
                        method: 'get',
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    });
                    const body = await result.json();
                    setHimnos(compararListas(body));
                    setLoading(false);
                }
                fetchData();
            //Si no hay nada en el campo buscar entonces se devuelven 
            //todos los himnos que existen en la Base de datos.
            } else {
                const fetchData = async () => {
                    const result = await fetch('api/himnos');
                    const body = await result.json();
                    setHimnos(compararListas(body));
                    setLoading(false);
                }
                fetchData();
            }
        //Buscar en los títulos de los himnos y también en la letra.
        } else {
            const estrofasId = new Set();
            let url = "";
            const fetchData = async () => {
                const result = await fetch('/api/buscarenestrofas?term='+ busqueda, {
                    method: 'get',
                    headers: {
                        'Content-Type': 'application/json',
                    }
                });
                const body = await result.json();
                for(var i=0; i < body.length; i++){
                    estrofasId.add(body[i].Himno_Id);
                }
                let idArray = Array.from(estrofasId);
                for(i = 0; i < idArray.length; i++){
                    url+= "&array[]=" + idArray[i];
                }

                const fetchData = async () => {
                    const result = await fetch('api/buscarencontenido?term='+ busqueda + url, {
                        method: 'get',
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    });
                    const body = await result.json();
                    setHimnos(compararListas(body));
                    setLoading(false);
                }
                fetchData();
            }
            fetchData();
        }
    }

    const checkboxChange = (event) => {
        let isChecked = event.target.checked;
        setBuscarContenido(isChecked);
    }

    const busquedaChange = (event) => {
        const termino = event.target.value;
        setBusqueda(termino)
        if(termino.length > 0){
            refBorrarTermino.current.classList.add('visible');
        } else {
            refBorrarTermino.current.classList.remove('visible');
        }
        
    }

    const borrarBusqueda = () => {
        setLoading(true);
        setBusqueda("");
        refBorrarTermino.current.classList.remove('visible');
        const fetchData = async () => {
            const result = await fetch('api/himnos');
            const body = await result.json();
            setHimnos(compararListas(body));
            setLoading(false);
        }
        fetchData();
    }
    
    const verHimno = (selected_id,selected_titulo) => {
        setID(selected_id);
        setTitulo(selected_titulo);
        toggleView();
    } 

    const agregarHimno = (selected_id,selected_titulo) => {
        let selectedHimnoIndex = himnos.findIndex(himno => himno.Id === selected_id);
        let himnosCopy = [...himnos];
        let updatedHimno = {...himnosCopy[selectedHimnoIndex]};
        
        if(!updatedHimno.selected){
            updatedHimno.selected = true;
            himnosCopy[selectedHimnoIndex] = updatedHimno;
            setHimnos(himnosCopy);

            let newHimno = [{ Id: selected_id, Titulo: selected_titulo}];
            let currentPresentacion = [...presentacion];
            const updatedPresentacion = currentPresentacion.concat(newHimno);
            setPresentacion(updatedPresentacion);
        }
    }

    const agregarElemento = (selected_id, selected_titulo, selected_tipo, selected_url) => {
        let selectedElementIndex = otrosElementos.findIndex(elemento => elemento.Id === selected_id);
        let elementsCopy = [...otrosElementos];
        let updatedElement = {...elementsCopy[selectedElementIndex]};
        let newElement =[{ Id: selected_id, Titulo: selected_titulo, tipo: selected_tipo, url:selected_url}];;
        
        if(!updatedElement.selected){
            updatedElement.selected = true;
            elementsCopy[selectedElementIndex] = updatedElement;
            setOtrosElementos(elementsCopy);
            
            let currentPresentacion = [...presentacion];
            const updatedPresentacion = currentPresentacion.concat(newElement);
            setPresentacion(updatedPresentacion);
        }
    }

    const bajarHimno = (selected_id) => {
        let selectedHimnoIndex = presentacion.findIndex(himno => himno.Id === selected_id);
        if(selectedHimnoIndex < (presentacion.length -1)){
            let currentObject = presentacion[selectedHimnoIndex];
            let updatedPresentacion = presentacion.filter( object => object.Id !== selected_id);
            updatedPresentacion.splice(selectedHimnoIndex + 1, 0, currentObject)
            setPresentacion(updatedPresentacion);
        }
    }
    const subirHimno = (selected_id) => {
        let selectedHimnoIndex = presentacion.findIndex(himno => himno.Id === selected_id);
        if(selectedHimnoIndex > 0){
            let currentObject = presentacion[selectedHimnoIndex];
            let updatedPresentacion = presentacion.filter( object => object.Id !== selected_id);
            updatedPresentacion.splice(selectedHimnoIndex - 1, 0, currentObject)
            setPresentacion(updatedPresentacion);
        }
    }
    const eliminarHimno = (selected_id) => {
        let updatedPresentacion = presentacion.filter( object => object.Id !== selected_id);
        setPresentacion(updatedPresentacion);

        let selectedHimnoIndex = himnos.findIndex(himno => himno.Id === selected_id);
        let himnosCopy = [...himnos];
        let updatedHimno = {...himnosCopy[selectedHimnoIndex]};
        updatedHimno.selected = false;
        himnosCopy[selectedHimnoIndex] = updatedHimno;
        setHimnos(himnosCopy);

        let selectedElementIndex = otrosElementos.findIndex(elemento => elemento.Id === selected_id);
        let elementsCopy = [...otrosElementos];
        let updatedElement = {...elementsCopy[selectedElementIndex]};
        updatedElement.selected = false;
        elementsCopy[selectedElementIndex] = updatedElement;
        setOtrosElementos(elementsCopy);
    }

    const toggleView = (event) => {
        if(!event) {
            refContainer.current.classList.toggle('visible');
        } else if(event.target.id === "himno-overlay" || event.target.id === "close-preview"){
            refContainer.current.classList.toggle('visible');
        }
    }

    const borrarPresentacion = () => {
        let himnosCopy = [...himnos];
        let elementsCopy = [...otrosElementos];
        presentacion.forEach((fila) => {
            if(!fila.tipo){
                let selectedHimnoIndex = himnos.findIndex(himno => himno.Id === fila.Id);
                let updatedHimno = {...himnosCopy[selectedHimnoIndex]};
                updatedHimno.selected = false;
                himnosCopy[selectedHimnoIndex] = updatedHimno;
            } else {
                let selectedElementIndex = otrosElementos.findIndex(elemento => elemento.Id === fila.Id);
                let updatedElement = {...elementsCopy[selectedElementIndex]};
                updatedElement.selected = false;
                elementsCopy[selectedElementIndex] = updatedElement;
            }
        });
        setOtrosElementos(elementsCopy);
        setHimnos(himnosCopy);
        setPresentacion([]);
    }

    const crearPresentacion = () => {
        setLoading(true);
        if(presentacion.length > 0){
            let nuevaPresentacion = new pptxgen();
            nuevaPresentacion.author = 'Creador de presentaciones';
            nuevaPresentacion.subject = 'Presentación de cantos';
            nuevaPresentacion.title = 'Presentación de cantos';
            let fetchArray = [];
            for(let i = 0; i < presentacion.length; i++){
                if(! presentacion[i].tipo)
                fetchArray.push(fetch('api/obtenerestrofas?id=' + presentacion[i].Id));
            }
            Promise.all(fetchArray)
            .then((responses) => {
                return Promise.all(responses.map((response) => {
                    return response.json();
                }));
            })
            .then((contenidoHimnos) => {
                let himnosIndex = 0;
                presentacion.forEach((fila, index) => {
                    let slide = nuevaPresentacion.addSlide();
                    if(!fila.tipo){
                        slide.background = { path : oracionesBg};
                        slide.addText(
                            fila.Titulo,
                            {
                                x: 0,
                                y: 1.51,
                                h: 2.61,
                                w: "100%",
                                color: 'FFFFFF',
                                fontSize: 60,
                                bold: true,
                                align: nuevaPresentacion.AlignH.center
                            }
                        );
                        let himno = contenidoHimnos[himnosIndex];
                        himno.forEach( (estrofa, i) => {
                            slide = nuevaPresentacion.addSlide();
                            slide.background = { color : "000000"};
                            slide.background = { path : estrofaBg};
                            slide.addText(
                                estrofa.Numero_estrofa,
                                {
                                    x: 0.08,
                                    y: 0,
                                    w: 0.5,
                                    h: 0.5,
                                    color: 'FFFFFF',
                                    align: nuevaPresentacion.AlignH.left
                                }
                            );
                            let estrofaFontSize = 50;
                            let contenido = estrofa.Contenido.replace(/[\r\n]+$/, '');
                            let cantidadLineas = contenido.split(/\r\n|\r|\n/).length;
                            if(cantidadLineas >= 6 && cantidadLineas < 8) estrofaFontSize = 46;
                            else if(cantidadLineas > 7 ) estrofaFontSize = 40;
                            else estrofaFontSize = 50;
                            slide.addText(
                                contenido,
                                {
                                    isTextBox: true,
                                    shrinkText: true,
                                    autofit: true,
                                    x: 0,
                                    y: 0,
                                    w: "100%",
                                    h: "100%",
                                    fontSize: estrofaFontSize,
                                    color: 'FFFFFF',
                                    align: nuevaPresentacion.AlignH.center
                                }
                            );
                            if(i === himno.length - 1){
                                slide.addText(
                                    "Fin",
                                    {
                                        x: 9.42,
                                        y: 5.145,
                                        w: 0.54,
                                        h: 0.42,
                                        color: 'FFFFFF',
                                        align: nuevaPresentacion.AlignH.left
                                    }
                                );
                            }
                        });
                        himnosIndex += 1;
                    } else if(fila.tipo === "oracion"){
                        slide.background = { path : oracionesBg};
                        slide.addText(
                            fila.Titulo,
                            {
                                x: 0,
                                y: 1.51,
                                h: 2.61,
                                w: "100%",
                                color: 'FFFFFF',
                                fontSize: 60,
                                bold: true,
                                align: nuevaPresentacion.AlignH.center
                            }
                        );
                    } else if(fila.tipo === "imagen"){
                        if(fila.Titulo === "Bienvenida")
                            slide.background = { path : bienvenida};
                        else 
                        slide.background = { path : anuncios};
                    }
                });
                nuevaPresentacion.writeFile({fileName: "Presentacion de prueba.pptx"});
                setLoading(false);
            })
            .catch((error) => {
                console.error(error);
            });
        }
    }

    const ocultarMostrarListaElementos = () => {
        refOtrosElementos.current.classList.toggle('visible');
    }
  
    useEffect(() => {
        setLoading(true);
        const fetchData = async () => {
            const result = await fetch('api/himnos');
            const body = await result.json();
            setHimnos(body);
            setLoading(false);
        }
        fetchData();

    }, []);

    const checkClickArea = (event) => {
        //console.log
        if (!refOtrosElementos.current.contains(event.target) && !otrosButton.current.contains(event.target)){
            refOtrosElementos.current.classList.remove('visible');
        } 
    }

    return(
        <React.Fragment>
            <main id="crearContainer" className="mainContainer" onClick={checkClickArea}>
                <header className="two-columns">
                    <div className="header-container">
                        <div className="header-left">
                        </div>
                        <div className="header-right">
                            <img id="engranajes" className="image-header" src={engranajes} alt="" />
                        </div>  
                    </div>  
                    <div className="mas-abajo" onClick={() => {refColumns.current.scrollIntoView({behavior: 'smooth'}); }}></div>
                </header>
                <div className="linea-divisoria"></div>
                <div className="columnsContainer" ref={refColumns}>
                    <div className="column column1">
                        <section>
                            <h2>Lista de cantos disponibles</h2>
                            <form onSubmit={buscarHimno}>
                                <div id="buscar-container">
                                    <button type="submit"><i className="icono icono-buscar"></i></button>
                                    <input type="text" name="search" placeholder="Buscar Canto" value={busqueda} onChange={busquedaChange}/>
                                    <div onClick={borrarBusqueda}><i id="borrar-termino" className="icono icono-cerrar" ref={refBorrarTermino}></i></div>
                                </div>
                                <label>
                                Buscar en contenido <input type="checkbox" onChange={checkboxChange}/>
                                </label>
                            </form>
                            <HimnosLista himnos={himnos} verHimno={verHimno} agregarHimno={agregarHimno}/>
                        </section>
                    </div>
                    <div className="column column2">
                        <section>
                            <h2>Lista de elementos en la presentación</h2>
                            <div id="iconos-presentacion">
                                <div className="icono">
                                    <i ref={otrosButton} className="icono-crear" onClick={ocultarMostrarListaElementos}></i>
                                    Otros
                                </div>
                                <div className="icono">
                                    <i className="icono-descargar" onClick={crearPresentacion}></i>
                                    Descargar
                                </div>
                                <div className="icono">
                                    <i className="icono-borrar-todo" onClick={borrarPresentacion}></i>
                                    Borrar lista
                                </div>
                            </div>
                            <OtrosElementosLista ref={refOtrosElementos} otrosElementos={otrosElementos} agregarElemento={agregarElemento}/>
                            <PresentacionLista presentacion={presentacion} bajarHimno={bajarHimno} subirHimno={subirHimno} eliminarHimno={eliminarHimno}/>
                        </section>
                    </div>
                </div>
                <div ref={refContainer} id="himno-overlay" onClick={toggleView}>
                    <VistaHimno titulo={titulo} id={id}/>
                </div>
                
            </main>
            <Loader className={"loader" + (loading?" visible":"")}  type="ThreeDots" color="#ACD25D" height={80} width={80} />
        </React.Fragment>   
    );
};

export default CrearPage;