import React, { useState, useEffect } from 'react';

const HimnosLista = (props) => {
    const maxPerPage = 16;
    const numberOfPages = Math.ceil(props.himnos.length / maxPerPage);
    const [pageNumber, setPageNumber] = useState(1);


    const next = () => {
        console.log('next');
        if(pageNumber < numberOfPages){
            setPageNumber(pageNumber + 1);
        }
    }

    const prev = () => {
        console.log('prev');
        if(pageNumber > 1){
            setPageNumber(pageNumber - 1);
        }
    }

    useEffect(() => {
        setPageNumber(1);
    }, [numberOfPages])

    return(
        <>
            <div id="lista-cantos">
                {props.himnos.map((himno, index) => {
                    if(index >= (maxPerPage * pageNumber - maxPerPage) && index < (maxPerPage * pageNumber)){
                        return(
                            <div className={"titulo-container" + ((himno.selected)? " titulo-selected" : "")} key={index}>
                                <div className="titulo">
                                    <h4>{himno.Titulo}</h4>
                                </div>
                                <div className="iconos">
                                    <i className="icono icono-ver" onClick={() => props.verHimno(himno.Id,himno.Titulo)}></i>
                                    <i className={"icono icono-agregar" + ((himno.selected)? " icono-selected" : "")} onClick={() => props.agregarHimno(himno.Id,himno.Titulo)}></i>
                                </div>
                            </div>
                        )
                    } else {
                        return(<div key={index}></div>);
                    }
                })}
                <div id="paginas">
                    <div id="flecha-anterior" ><i className="icono icono-abajo" onClick={prev}></i></div><h3>Página {pageNumber}/{numberOfPages}</h3><div id="flecha-siguiente"><i className="icono icono-abajo" onClick={next}></i></div>
                </div>
            </div>
        </>
    );
};

export default HimnosLista;