import React, { Component } from "react";
import youtube_logo from './images/youtube_logo.svg';
import facebook_logo from './images/facebook_logo.svg';

class Footer extends Component {

    render() {
        return (
            <React.Fragment>
                <footer>
                    <p>Iglesia de Cristo en Coronado, Costa Rica.</p>
                    <img id="youtube_logo" className="footer_logo" src={youtube_logo} alt="Logo de Youtube" onClick={()=> window.open("https://www.youtube.com/channel/UCqIKSj2WYN4mEgRKVZ-6Zow", "_blank")}/>
                    <img id="facebook_logo" className="footer_logo" src={facebook_logo} alt="Logo de Facebook" onClick={()=> window.open("https://www.facebook.com/iglesiadecristo.coronado", "_blank")}/>
                </footer>
            </React.Fragment>  
        );
    } 
}

export default Footer;