import React, {useRef} from 'react';
import TextareaAutosize from 'react-textarea-autosize';

const EstrofasModificables = (props) => {
    const refEstrofas = useRef();

    const handleChange = (estrofa, e) => {
        props.updateEstrofa(estrofa.Numero_estrofa, e)
    }

    const keyPressed = (event) => {
        var textarea = event.target,
            text = textarea.value,
            numberOfLines = (text.match(/\n/g) || []).length + 1,
            maxRows = 8;
            console.log(numberOfLines);
        if (event.which === 13 && numberOfLines >= maxRows ) {
          event.preventDefault();
          return false;
        }
    }
    return(
        <>
            <div id="estrofas-inputs" ref={refEstrofas}>
                {props.estrofas.map((estrofa, key) => (
                    <div className="textarea-container" key={key}>
                        <TextareaAutosize maxLength="272" onKeyPress={keyPressed} className="estrofa-textarea" maxRows="8" cols="32" value={estrofa.Contenido} onChange={(e) => handleChange(estrofa, e)} />
                        <h4>Diapositiva {estrofa.Numero_estrofa}</h4>
                        <i className="icono icono-cerrar" onClick={() => props.borrarEstrofa(estrofa.Numero_estrofa)}></i>
                    </div>
                ))}
            </div>
        </>
    );
};

export default EstrofasModificables;