import React, { useState, createRef, useRef} from 'react';
import BusquedaLista from '../components/BusquedaLista';
import EstrofasModificables from '../components/EstrofasModificables';
import edit_image from '../images/edit_image.svg';
import Loader from "react-loader-spinner";
import { usePopup } from 'react-hook-popup';

const ModificarPage = () => {
    const refColumns = useRef();
    const refBusquedaRes = createRef();
    const refOverlay = useRef();
    const refRelleno = useRef();
    const [selectedHimno, setSelectedHimno] = useState([{id: 0, titulo:"", cantidad: 0, updated: false}]);
    const [loading, setLoading] = useState(false);
    const [busqueda, setBusqueda] = useState("");
    const [himnos, setHimnos] = useState([{ Id: 0, Titulo: "", Cantidad_estrofas: 0, selected: false }]);
    const [estrofas, setEstrofas] = useState([]);
    const [buscarContenido, setBuscarContenido] = useState(false);
    const [confirm] = usePopup('confirm', ({ message, confirm, cancel }) => (
        <div className="confirm-modal modal">
            ¿Está seguro que desea ELIMINAR el canto <br/>"{selectedHimno[0].titulo}"?<br/><span>Después de eliminar un canto este ya no estará disponible.</span>
            <button className="eliminar-btn" onClick={confirm}>Eliminar</button>
            <button className="cancelar-btn" onClick={cancel}>Cancelar</button>
        </div>
    ));
    const [guardar] = usePopup('guardar', ({ message, confirm, cancel }) => (
        <div className="confirm-guardar modal">
            ¿Está seguro que desea Guardar los cambios de este canto en la base de datos?
            <button className="aceptar-btn" onClick={confirm}>Aceptar</button>
            <button className="cancelar-btn" onClick={cancel}>Cancelar</button>
        </div>
    ));


    const modificarSelectedHimno = (propiedades, valor) => {
        let selectedHimnoCopy = [...selectedHimno];
        propiedades.forEach((propiedad, index) => {
            selectedHimnoCopy[0][propiedad] = valor[index];
        });
        setSelectedHimno(selectedHimnoCopy);
    }

    //Buscar los himnos que coincidan con el texto de busqueda cuando el botón buscar es presionado.
    const buscarHimno = (event) => {
        event.preventDefault();
        modificarSelectedHimno(["updated"], [false]);
        setEstrofas([]);
        setLoading(true);
        //Buscar solo en los títulos de los himnos.
        if(!buscarContenido){
            if(busqueda.length > 0){
                fetch('api/buscartitulos?term='+ busqueda, {
                        method: 'get',
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    })
                .then(response => response.json())
                .then(result => {
                    setLoading(false);
                    if(result.length > 1){
                        refOverlay.current.style.display = "block";
                        setHimnos(result);
                    } else if(result.length > 0){
                        desplegarEstrofas(result[0].Id, result[0].Titulo);
                    } else {
                        alert("No hay coincidencias con su busqueda");
                        setLoading(false);
                    }
                })
                .catch((error) => {
                    console.error(error);
                    alert("¡Error en la conexión!");
                    setLoading(false);
                });
            } else {
                alert("La busqueda está vacía. Digite algun texto para buscar.");
                setLoading(false);
            }
        //Buscar en los títulos de los himnos y también en la letra.
        } else {
            if(busqueda.length > 0){
                const estrofasId = new Set();
                let url = "";
                fetch('/api/buscarenestrofas?term='+ busqueda, {
                        method: 'get',
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    })
                .then(response => response.json())
                .then(result => {
                    for(var i=0; i < result.length; i++){
                        estrofasId.add(result[i].Himno_Id);
                    }
                    let idArray = Array.from(estrofasId);
                    for(i = 0; i < idArray.length; i++){
                        url+= "&array[]=" + idArray[i];
                    }
                    fetch('api/buscarencontenido?term='+ busqueda + url, {
                        method: 'get',
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    })
                    .then(response => response.json())
                    .then(result => {
                        setLoading(false);
                        if(result.length > 1){
                            refOverlay.current.style.display = "block";
                            setHimnos(result);    
                        } else if(result.length > 0){
                            desplegarEstrofas(result[0].Id, result[0].Titulo);
                        } else {
                            alert("No hay coincidencias con su busqueda");
                            setLoading(false);
                        }
                    })
                    .catch((error) => {
                        console.error(error);
                        alert("¡Error en la conexión!");
                        setLoading(false);
                    });
                })
                .catch((error) => {
                    console.error(error);
                    alert("¡Error en la conexión!");
                    setLoading(false);
                });
            } else {
                window.alert("La busqueda está vacía. Digite algun texto para buscar.");
                setLoading(false);
            }
        }
    }

    const checkboxChange = (event) => {
        let isChecked = event.target.checked;
        setBuscarContenido(isChecked);
    }

    const busquedaChange = (event) => {
        const termino = event.target.value;
        setBusqueda(termino);
        if(estrofas.length > 0){
            modificarSelectedHimno(["titulo", "updated"], [termino, true]);
        }
    }

    const desplegarEstrofas = (selected_id,selected_titulo) => {
        setLoading(true);
        refRelleno.current.style.display = "none";
        fetch('api/obtenerestrofas?id='+ selected_id)
        .then(response => response.json())
        .then(data => {
            setEstrofas(data); 
            setLoading(false);
            modificarSelectedHimno(["titulo", "cantidad", "id"], [selected_titulo, data.length, data[0].Himno_Id]);
        })
        .catch(error => console.error(error));
        setBusqueda(selected_titulo);
        refOverlay.current.style.display = "none";
    }

    const updateEstrofa = (numero_estrofa, event) => {
        let estrofasCopy = [...estrofas];
        let currentEstrofa = event.target.value;
        estrofasCopy[numero_estrofa - 1].Contenido = currentEstrofa;
        estrofasCopy[numero_estrofa - 1].updated = true;
        setEstrofas(estrofasCopy);
    }

    const borrarEstrofa = (numero) => {
        if(estrofas.length > 1){
            let estrofaIndex = numero - 1;
            console.log("estrofa index: " + estrofaIndex);
            let estrofasCopy = [...estrofas];
            estrofasCopy.splice(estrofaIndex, 1);

            estrofasCopy.forEach((estrofa) => {
                if(estrofa.Numero_estrofa >= estrofaIndex + 1){
                    estrofa.Numero_estrofa -= 1;
                    estrofa.updated = true;
                }
            });
            modificarSelectedHimno(["updated"], [true]);
            setEstrofas(estrofasCopy);
        } else {
            alert("No se puede elminar. Cada himno debe tener mínimo una estrofa.");
        }
        
    }

    const nuevaDiapositiva = () => {
        if(selectedHimno[0].id !== 0){
            let estrofasCopy = [...estrofas];
            estrofasCopy.push({Himno_Id: estrofasCopy[0].Himno_Id, Numero_estrofa: estrofasCopy.length + 1, Contenido: ""});
            setEstrofas(estrofasCopy);
        } else {
            alert("No ha seleccionado ningún canto para modificar.");
        }
    }

    const eliminarCanto = async() => {
        if(selectedHimno[0].cantidad > 0){
            const confirmed = await confirm();
            if (confirmed) {
                let fetchArray = [];
                let borrarData = {id: selectedHimno[0].id}
                fetchArray.push(
                    fetch('api/borrartodasestrofas?id=' + borrarData.id, {
                        method: 'delete',
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    })
                );
                fetchArray.push(
                    fetch('api/borrarhimno?id=' + borrarData.id, {
                        method: 'delete',
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    })
                );
                Promise.all(fetchArray)
                .then(() => {
                    setSelectedHimno([{id: 0, titulo:"", cantidad: 0, updated: false}]);
                    setEstrofas([]);
                    setHimnos([{ Id: 0, Titulo: "", Cantidad_estrofas: 0, selected: false }]);
                    setBusqueda("");
                    refRelleno.current.innerHTML = "<h1>El canto ha sido eliminado.</h1>";
                    refRelleno.current.style.display = "block";
                })
                .catch((error) => {
                    console.error(error);
                    setLoading(false);
                });
            } else {
                console.log("No ha pasado nada");
            } 
        } else {
            alert("No hay nada que eliminar. Selecciona un canto antes.")
        }
    }

    const actualizarHimno = async() => {
        if(selectedHimno[0].cantidad > 0){
            const confirmed = await guardar();
            if (confirmed) {
                setLoading(true);
                let fetchArray = [];
                let estrofaData = {};
                let himnoData = {titulo: selectedHimno[0].titulo, cantidad: estrofas.length, id: estrofas[0].Himno_Id};
                let borrarCantidad;
                //Borrar estrofas si es necesario, se borra de la última hacia atras.
                if(selectedHimno[0].cantidad > estrofas.length){
                    borrarCantidad = selectedHimno[0].cantidad - estrofas.length;
                    for(var i = 0; i < borrarCantidad; i++){
                        let borrarData = {id: himnoData.id, numero: selectedHimno[0].cantidad - i}
                        fetchArray.push(
                            fetch('api/borrarestrofa?id=' + borrarData.id + "&cantidad=" + borrarData.numero, {
                                method: 'delete',
                                headers: {
                                    'Content-Type': 'application/json',
                                }
                            })
                        );
                    }
                    let nuevaCantidad = selectedHimno[0].cantidad - borrarCantidad;
                    modificarSelectedHimno(["cantidad"], [nuevaCantidad]);
                //Insertar nuevas diapositivas
                } else if(selectedHimno[0].cantidad < estrofas.length){
                    let insertarCantidad =  estrofas.length - selectedHimno[0].cantidad;
                    for(i = 1; i <= insertarCantidad; i++){
                        let numero_estrofa = selectedHimno[0].cantidad + i;
                        let insertarData = {id: himnoData.id, numero: numero_estrofa, contenido: estrofas[numero_estrofa - 1].Contenido}
                        fetchArray.push(
                            fetch('api/insertarestrofa', {
                                method: 'post',
                                headers: {
                                    'Content-Type': 'application/json',
                                },
                                body: JSON.stringify(insertarData)
                            })
                        );
                        // console.log("La estrofa: " + insertarData.numero + " fue agregada. Contenido: ", insertarData.contenido);
                        let estrofasCopy = [...estrofas];
                        estrofasCopy[numero_estrofa - 1].updated = false;
                        setEstrofas(estrofasCopy);
                    }
                    let nuevaCantidad = selectedHimno[0].cantidad + insertarCantidad;
                    modificarSelectedHimno(["cantidad"], [nuevaCantidad]);
                }
                //Actualizar todas las diapositivas que hayan sido actualizadas.
                if(selectedHimno[0].updated){
                    fetchArray.push(
                        fetch('api/actualizartitulo', {
                                method: 'post',
                                headers: {
                                    'Content-Type': 'application/json',
                                },
                                body: JSON.stringify(himnoData)
                            })
                    );
                    // console.log("El himno ha sido actualizado.", himnoData);
                    modificarSelectedHimno(["updated"], [false]);
                } else {
                    console.log("El himno NO ha sido actualizado");
                }

                let estrofasCopy = [...estrofas];
                estrofasCopy.forEach(estrofa => {
                    if(estrofa.updated){
                        estrofaData = {id: estrofa.Himno_Id, numero: estrofa.Numero_estrofa, contenido: estrofa.Contenido}
                        fetchArray.push(
                            fetch('api/actualizarestrofa', {
                                    method: 'post',
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify(estrofaData)
                                })
                        );
                        // console.log("La estrofa: " + estrofaData.numero + " ha sido actualizada.");
                        estrofa.updated = false;
                    } else {
                        console.log("La estrofa:" + estrofa.Numero_estrofa + " NO ha sido actualizado");
                    }

                });
                
                Promise.all(fetchArray)
                .then(() => {
                    setEstrofas([]);
                    setSelectedHimno([{id: 0, titulo:"", cantidad: 0, updated: false}]);
                    setBusqueda("");
                    refRelleno.current.style.display = "block";
                    refRelleno.current.innerHTML = "<h1>El canto ha sido actualizado.</h1>";
                    setLoading(false);
                })
                .catch((error) => {
                    console.error(error);
                    setLoading(false);
                });
            }
        } else {
            alert("No hay nada que guardar.");
        }
        
    }

    const cerrarOverlay = () => {
        refOverlay.current.style.display = "none";
    }

    return(
        <React.Fragment>
            <main id="modificarContainer" className="mainContainer">
                <header className="two-columns">
                <div className="header-container">
                        <div className="header-left">
                        </div>
                        <div className="header-right">
                            <img id="edit-image" className="image-header" src={edit_image} alt="" />
                        </div>  
                    </div>  
                    <div className="mas-abajo" onClick={() => {refColumns.current.scrollIntoView({behavior: 'smooth'}); }}></div>
                </header>
                <div className="linea-divisoria"></div>
                <div className="columnsContainer" ref={refColumns}>
                    <div className="column column1">
                        <section>
                            <form onSubmit={buscarHimno}>
                                <div className="titulo-modificar">
                                    <h2>Título:</h2>
                                    <div className="buscar-container">
                                        <input type="text" name="search" placeholder="Buscar Canto" value={busqueda} onChange={busquedaChange}/>
                                    </div>
                                </div>
                                <button type="submit">Buscar</button>
                                <label>
                                Buscar en contenido <input type="checkbox" onChange={checkboxChange}/>
                                </label>
                            </form>
                        </section>
                    </div>
                    <div className="column column2">
                        <section>
                            <h2 className="cantidad-header">Cantidad de estrofas:</h2>
                            <div className="numero">{estrofas.length}</div>
                            <div id="modify-icons">
                            <div className="guardar-container" onClick={nuevaDiapositiva}>
                                    <i className="icono-nueva"></i>
                                    Nueva Diapositiva
                                </div>
                                <div className="guardar-container" onClick={actualizarHimno}>
                                    <i className="icono-salvar"></i>
                                    Guardar Cambios
                                </div>
                                <div className="guardar-container" onClick={eliminarCanto}>
                                    <i className="icono-eliminar"></i>
                                    Eliminar Canto
                                </div>
                            </div>
                        </section>
                    </div>
                    
                </div>
            <div className="overlay" ref={refOverlay}>
                <BusquedaLista ref={refBusquedaRes} himnos={himnos} desplegarEstrofas={desplegarEstrofas} cerrarOverlay={cerrarOverlay}/>
            </div>
            <EstrofasModificables estrofas={estrofas} updateEstrofa={updateEstrofa} borrarEstrofa={borrarEstrofa}/>
            <div ref={refRelleno} className="relleno"><h1>Escoja el canto que desea modificar.</h1></div>
            </main>
            <Loader className={"loader" + (loading?" visible":"")}  type="ThreeDots" color="#80B041" height={80} width={80} />
        </React.Fragment>   
    )
    
};

export default ModificarPage;