import React from 'react';

const PresentacionLista = (props) => {

    return(
        <>
        <div id="presentacion-cantos">
        {props.presentacion.map((himno, key) => (
            <div className={"titulo-container" + ((himno.tipo)? " "+himno.tipo : "")} key={key}>
                <div className="titulo">
                    <h4>{himno.Titulo}</h4>
                </div>
                <div className="iconos">
                    <i className="icono icono-abajo" onClick={() => props.bajarHimno(himno.Id)}></i>
                    <i className="icono icono-arriba" onClick={() => props.subirHimno(himno.Id)}></i>
                    <i className="icono icono-cerrar" onClick={() => props.eliminarHimno(himno.Id)}></i>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default PresentacionLista;