import React, { useState, createRef, useRef} from 'react';
import BusquedaLista from '../components/BusquedaLista';
import EstrofasModificables from '../components/EstrofasModificables';
import Loader from "react-loader-spinner";
import { usePopup } from 'react-hook-popup';
import agregar_image from '../images/agregar_image.svg';
const AgregarPage = () => {
    const refColumns = useRef();
    const refBusquedaRes = createRef();
    const refOverlay = useRef();
    const refRelleno = useRef();
    const [loading, setLoading] = useState(false);
    const [busqueda, setBusqueda] = useState("");
    const [himnos, setHimnos] = useState([{ Id: 0, Titulo: "", Cantidad_estrofas: 0, selected: false }]);
    const [estrofas, setEstrofas] = useState([]);
    const [confirm] = usePopup('confirm', ({ message, confirm, cancel }) => (
        <div className="confirm-guardar modal">
            ¿Está seguro que desea AGREGAR este canto a la base de datos?<br/><span>Después de agregar un himno este se puede modificar en la sección respectiva.</span>
            <button className="aceptar-btn" onClick={confirm}>Aceptar</button>
            <button className="cancelar-btn" onClick={cancel}>Cancelar</button>
        </div>
    ));


    //Buscar los himnos que coincidan con el texto de busqueda cuando el botón buscar es presionado.
    const verificarTitulo = (event) => {
        event.preventDefault();
        setLoading(true);
        //Buscar solo en los títulos de los himnos.
        if(busqueda.length > 0){
            fetch('api/buscartitulo?term='+ busqueda, {
                    method: 'get',
                    headers: {
                        'Content-Type': 'application/json',
                    }
                })
            .then(response => response.json())
            .then(result => {
                if(result.length > 1){
                    refOverlay.current.style.display = "block";
                    setHimnos(result);
                    setLoading(false);
                } else if(result.length > 0){
                    desplegarEstrofas(result[0].Id, result[0].Titulo);
                    setLoading(false);
                } else {
                    alert("No hay ningún título que coincida.");
                    setLoading(false);
                }
            })
            .catch((error) => {
                console.error(error);
                alert("¡Error en la conexión!");
                setLoading(false);
            });
        } else {
            alert("La busqueda está vacía. Digite algun texto para buscar.");
            setLoading(false);
        }
    }

    const busquedaChange = (event) => {
        const termino = event.target.value;
        setBusqueda(termino);
    }

    const desplegarEstrofas = (selected_id,selected_titulo) => {
        setLoading(true);
        fetch('api/obtenerestrofas?id='+ selected_id)
        .then(response => response.json())
        .then(data => {
            setEstrofas(data); 
            setLoading(false);
            refRelleno.current.style.display = "none";
        })
        .catch(error => console.error(error));
        setBusqueda(selected_titulo);
        refOverlay.current.style.display = "none";
    }

    const updateEstrofa = (numero_estrofa, event) => {
        let estrofasCopy = [...estrofas];
        let currentEstrofa = event.target.value;
        estrofasCopy[numero_estrofa - 1].Contenido = currentEstrofa;
        setEstrofas(estrofasCopy);
    }

    const borrarEstrofa = (numero) => {
        if(estrofas.length > 1){
            let estrofaIndex = numero - 1;
            let estrofasCopy = [...estrofas];
            estrofasCopy.splice(estrofaIndex, 1);

            estrofasCopy.forEach((estrofa) => {
                if(estrofa.Numero_estrofa >= estrofaIndex + 1){
                    estrofa.Numero_estrofa -= 1;
                    estrofa.updated = true;
                }
            });
            setEstrofas(estrofasCopy);
        } else {
            alert("No se puede eliminar. Cada himno debe tener mínimo una estrofa.");
        }
    }

    const nuevaDiapositiva = () => {
        let estrofasCopy = [...estrofas];
        estrofasCopy.push({Himno_Id: null, Numero_estrofa: estrofasCopy.length + 1, Contenido: ""});
        setEstrofas(estrofasCopy);
        refRelleno.current.style.display = "none";
    }

    const borrarContenido = () => {
        setEstrofas([]);
        refRelleno.current.style.display = "block";
    }

    const guardarCanto = async() => {
        if(estrofas.length > 0 && busqueda.length > 1){
            const confirmed = await confirm();
            if(confirmed){
                setLoading(true);
                console.log(busqueda, estrofas);
                let fetchArray = [];
                let himnoData = {titulo: busqueda, cantidad: estrofas.length};
                fetch('api/insertarhimno', {
                    method: 'post',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(himnoData)
                })
                .then((response) => response.json())
                .then((data) => {
                    let himnoId = data.insertId;
                    estrofas.forEach((estrofa, index) => {
                        let numero_estrofa = index + 1;
                        let insertarData = {id: himnoId, numero: numero_estrofa, contenido: estrofa.Contenido}
                        fetchArray.push(
                            fetch('api/insertarestrofa', {
                                method: 'post',
                                headers: {
                                    'Content-Type': 'application/json',
                                },
                                body: JSON.stringify(insertarData)
                            })
                        );
                    })
                    Promise.allSettled(fetchArray)
                    .then((results) => {
                        console.log(results);
                        setEstrofas([]);
                        refRelleno.current.innerHTML = '<h1>El canto "' + busqueda + '" ha sido agregado a la Base de datos.</h1>';
                        refRelleno.current.style.display = "block";
                        setBusqueda("");
                        setLoading(false);
                    })
                    .catch((error) => {
                        console.error(error);
                        setLoading(false);
                    });
                })
                .catch((error) => {
                    console.error(error);
                    alert("¡Error en la conexión!");
                    setLoading(false);
                });
            }
            
        } else {
            if(estrofas.length === 0) alert("Debe haber mínimo una estrofa");
            else if(busqueda.length === 0) alert("Se necesita un título para el canto.");
        }
    }

    const cerrarOverlay = () => {
        refOverlay.current.style.display = "none";
    }

    return(
        <React.Fragment>
            <main id="agregarContainer" className="mainContainer">
                <header className="two-columns">
                    <div className="header-container">
                            <div className="header-left">
                            </div>
                            <div className="header-right">
                                <img id="agregar" className="image-header" src={agregar_image} alt="" />
                            </div>  
                        </div>  
                        <div className="mas-abajo" onClick={() => {refColumns.current.scrollIntoView({behavior: 'smooth'}); }}></div>    
                </header>
                <div className="linea-divisoria"></div>
                <div className="columnsContainer" ref={refColumns}>
                    <div className="column column1">
                        <section>
                            <form onSubmit={verificarTitulo}>
                                <div className="titulo-modificar">
                                    <h2>Título:</h2>
                                    <div className="buscar-container">
                                        <input type="text" name="search" placeholder="Buscar Canto" value={busqueda} onChange={busquedaChange}/>
                                    </div>
                                </div>
                                <button type="submit">Verificar</button>
                            </form>
                        </section>
                    </div>
                    <div className="column column2">
                        <section>
                            <h2 className="cantidad-header">Cantidad de estrofas:</h2>
                            <div className="numero">{estrofas.length}</div>
                            <div id="add-icons">
                            <div className="guardar-container" onClick={nuevaDiapositiva}>
                                    <i className="icono-nueva"></i>
                                    Nueva Diapositiva
                                </div>
                                <div className="guardar-container" onClick={guardarCanto}>
                                    <i className="icono-salvar"></i>
                                    Guardar Canto
                                </div>
                                <div className="guardar-container" onClick={borrarContenido}>
                                    <i className="icono-borrar-todo"></i>
                                    Borrar todo
                                </div>
                            </div>
                        </section>
                    </div>
                    
                </div>
            <div className="overlay" ref={refOverlay}>
                <BusquedaLista ref={refBusquedaRes} himnos={himnos} desplegarEstrofas={desplegarEstrofas} cerrarOverlay={cerrarOverlay}/>
            </div>
            <EstrofasModificables estrofas={estrofas} updateEstrofa={updateEstrofa} borrarEstrofa={borrarEstrofa}/>
            <div ref={refRelleno} className="relleno"><h1>Escoja un título y agregue diapositivas.</h1></div>
            </main>
            <Loader className={"loader" + (loading?" visible":"")}  type="ThreeDots" color="#80B041" height={80} width={80} />
        </React.Fragment>   
    )
}

export default AgregarPage;