import React, { forwardRef } from 'react';

const OtrosElementosLista = forwardRef((props, ref) => {
    return(
        <>
        <div ref={ref} id="lista-elementos">
        {props.otrosElementos.map((elemento, key) => (
            <div className={"titulo-container" + ((elemento.selected)? " titulo-selected" : "")} key={key} onClick={() => props.agregarElemento(elemento.Id,elemento.Titulo,elemento.tipo,elemento.url)}>
                <div className="titulo">
                    <h4>{elemento.Titulo}</h4>
                </div>
                <div className="iconos-ele">
                    <i className={"icono icono-agregar" + ((elemento.selected)? " icono-selected" : "")}></i>
                </div>
            </div>
        ))}
        </div>
        </>
    );
});

export default OtrosElementosLista;