import {
  BrowserRouter as Router,
  Route,
  Switch,
} from 'react-router-dom';
import TopNav from './TopNav';
import Footer from './Footer';
import './App.css';
import HomePage from './pages/HomePage';
import CrearPage from './pages/CrearPage';
import AgregarPage from './pages/AgregarPage';
import ModificarPage from './pages/ModificarPage';
import NotFoundPage from './pages/NotFoundPage';
import { PopupProvider } from 'react-hook-popup';

function App() {
  return (
    <Router>
      <PopupProvider>
        <div className="App">
          <TopNav />
          <div id="page-body">
            <Switch>
              <Route path="/" component={HomePage} exact />
              <Route path="/crear" component={CrearPage} />
              <Route path="/agregar" component={AgregarPage} />
              <Route path="/modificar" component={ModificarPage} />
              <Route component={NotFoundPage} />
            </Switch>
          </div>
          <Footer/>
        </div>
      </PopupProvider>
      
    </Router>
  );
}

export default App;
