import React, { Component } from "react";
import { NavLink } from 'react-router-dom';
import mainLogo from './images/Logo-Coronado.svg';

class TopNav extends Component {

    state = { isActive: false };

    handleToggle = () => {
        this.setState({ isActive: !this.state.isActive });
    };

    render() {
        const isActive = this.state.isActive;
        return (

            <React.Fragment>
                <div className="topBar"><div id="menu-bars" onClick={this.handleToggle}></div></div>
                <nav className="no-mobile">
                    <div className="no-mobile-container">
                        <img className="logo" src={mainLogo} alt="Logo Iglesia de Cristo en Coronado" />
                        <ul>
                            <li>
                                <NavLink to="/" exact>
                                    Inicio
                                    <div className="active-line"></div>
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to="/crear">
                                    Crear
                                    <div className="active-line"></div>
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to="/agregar">
                                    Agregar
                                    <div className="active-line"></div>
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to="/modificar">
                                    Modificar
                                    <div className="active-line"></div>
                                </NavLink>
                            </li>
                        </ul>
                    </div>
                </nav>
                <nav className="navMobile">
                    <ul className={isActive ? "active" : ""}>
                        <li>
                            <NavLink to="/" exact onClick={this.handleToggle}>
                                Inicio
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to="/crear" onClick={this.handleToggle}>
                                Crear
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to="/agregar" onClick={this.handleToggle}>
                                Agregar
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to="/modificar" onClick={this.handleToggle}>
                                Modificar
                            </NavLink>
                        </li>
                    </ul>
                    <img className="logo" src={mainLogo} alt="Logo Iglesia de Cristo en Coronado" />
                </nav>
            </React.Fragment>  
        );
    } 
}

export default TopNav;