import React, { useState, useEffect} from 'react';
import Loader from "react-loader-spinner";

const VistaHimno = ({ titulo, id }) => {

    const [estrofas, setEstrofas] = useState([{ Himno_Id: 1, Numero_estrofa: 1, Contenido: "" }]);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        setLoading(true);
        const fetchData = async () => {
            const result = await fetch('/api/obtenerestrofas?id='+id, {
                method: 'get',
                headers: {
                    'Content-Type': 'application/json',
                }
            });
            const body = await result.json();
            setEstrofas(body);
            setLoading(false);
        }
        fetchData();
    }, [id]);


    return(
        <div className="preview-container">
            <h1 id="titulo-canto">{titulo}</h1>
            {estrofas.map((estrofa, key) => { 
                var contenido = (estrofa.Contenido.replace(/(?:\r\n|\r|\n)/g, '<br>'));
                return(
                <div className="estrofa" key={key} dangerouslySetInnerHTML={{__html: contenido}}>
                </div>
            )})}
            <i id="close-preview" className="icono icono-cerrar"></i>
            <Loader className={"loader" + (loading?" visible":"")}  type="ThreeDots" color="#80B041" height={80} width={80} />
        </div>
    );
};

export default VistaHimno;