import React, { forwardRef } from 'react';

const BusquedaLista = forwardRef((props, ref) => {
    return(
        <>
        <div ref={ref} id="lista-busqueda">
            <i className="icono icono-cerrar" onClick={props.cerrarOverlay}></i>
            <p>Hay más de un canto que coincide con su busqueda por favor</p>
            <h3>Elija el canto que desea modificar.</h3>
            {props.himnos.map((elemento, key) => (
                <div className={"titulo-container" + ((elemento.selected)? " titulo-selected" : "")} key={key} onClick={() => props.desplegarEstrofas(elemento.Id,elemento.Titulo)}>
                    <div className="titulo">
                        <h4>{elemento.Titulo}</h4>
                    </div>
                    <div className="iconos-ele">
                        <i className={"icono icono-agregar" + ((elemento.selected)? " icono-selected" : "")}></i>
                    </div>
                </div>
            ))}
        </div>
        </>
    );
});

export default BusquedaLista;